<template>
  <div class="CustomerData">
    <div
      class="CustomerDataBox"
      v-for="(item, index) in dataFormList"
      :key="index"
    >
      <div
        class="CustomerDataHeader"
        @click="
          clickStatus(true, index);
          GetCustomerRemark(item.CustId, index);
        "
      >
        <img
          v-if="!item.CustPictureUrl"
          class="CustomerDataHeaderImgA"
          draggable="false"
          src="@/assets/image/InstallHeadPortrait.png"
          alt=""
        />
        <img
          v-if="item.CustPictureUrl"
          class="CustomerDataHeaderImgA"
          draggable="false"
          :src="item.CustPictureUrl"
          alt=""
        />
        <span>
          {{ item.Name }}
        </span>
        <img
          v-if="item.IsFromOpportunity"
          class="CustomerDataHeaderImgB"
          draggable="false"
          src="@/assets/image/commercialOpportunity.png"
          alt=""
        />
        <!-- <img
          @click="
            clickStatus(true, index);
            GetCustomerRemark(item.CustId, index);
          "
          v-if="!statusList[index]"
          class="CustomerDataHeaderImgRight"
          draggable="false"
          src="@/assets/image/right.png"
          alt=""
        />
        <img
          @click="clickStatus(false, index)"
          v-if="statusList[index]"
          class="CustomerDataHeaderImgBottom"
          draggable="false"
          src="@/assets/image/bottom.png"
          alt=""
        /> -->
      </div>
      <div class="CustomerDataContent" v-if="statusList[index]">
        <h2>地址：{{ item.Address }}</h2>
        <h2>電話：{{ item.Mobile }}</h2>
        <p>{{ item.Remark }}</p>
        <div @click="toCustomerDataDetails(item.CustId, item.Mobile)">
          查看詳情
        </div>
      </div>
    </div>
    <!-- 空组件 -->
    <noContent v-if="dataFormListStatus" />
    <!-- 加载中组件 -->
    <Loading :show="loadingShow" />
  </div>
</template>

<script>
import noContent from "@/components/noContent";
import Loading from "@/components/Loading";
import {
  POST_GetMyCustomers,
  POST_GetCustomerRemark,
  POST_GetCustomerByDistributtion,
} from "@/utils/api";
export default {
  name: "CustomerData",
  components: {
    noContent,
    Loading,
  },
  data() {
    return {
      loadingShow: false,
      dataFormListStatus: false,
      statusList: [],
      dataFormList: [
        // {
        //   headerImgUrl: require("@/assets/image/InstallHeadPortrait.png"),
        //   name: "王阿英",
        //   iconImgUrl: require("@/assets/image/commercialOpportunity.png"),
        //   location: "台中市西屯區南海路2號2樓",
        //   phone: "0923452345",
        //   content:
        //     "2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。"
        // },
        // {
        //   headerImgUrl: require("@/assets/image/InstallHeadPortrait.png"),
        //   name: "陳大可",
        //   iconImgUrl: "",
        //   location: "台中市西屯區南海路2號2樓",
        //   phone: "0923452345",
        //   content:
        //     "2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。"
        // },
        // {
        //   headerImgUrl: require("@/assets/image/InstallHeadPortrait.png"),
        //   name: "张樱花",
        //   iconImgUrl: require("@/assets/image/commercialOpportunity.png"),
        //   location: "台中市西屯區南海路2號2樓",
        //   phone: "0923452345",
        //   content:
        //     "2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。2019年12月購入新的淨水器機器家中現在有三台櫻花淨水器等設備。"
        // }
      ],
    };
  },
  created() {
    this.GetMyCustomers();
  },
  methods: {
    // 获取客户资料
    GetMyCustomers() {
      this.loadingShow = true;
      if (this.$route.query.CustPhone) {
        var UofId = {
          CustPhone: this.$route.query.CustPhone,
          DistributionUofId: sessionStorage.getItem("DistributionUofId"),
        };
        // 搜索API
        POST_GetCustomerByDistributtion(UofId).then((response) => {
          // 请求返回状态判断
          if (response.State) {
            this.dataFormList.push(response.Data);
            for (var i = 0; i < this.dataFormList.length; i++) {
              this.statusList.push(false);
            }
            this.dataFormListStatus = false;
          } else {
            this.dataFormListStatus = true;
          }
          // 判断列表没有数据是显示空组件
          if (this.dataFormList.length == 0) {
            this.dataFormListStatus = true;
          } else {
            this.dataFormListStatus = false;
          }
          this.loadingShow = false;
        });
      } else {
        var UofIds = {
          DistributionUofId: sessionStorage.getItem("DistributionUofId"),
        };
        // 列表请求API
        POST_GetMyCustomers(UofIds).then((response) => {
          // 请求返回状态判断
          if (response.State) {
            this.dataFormList = response.Data;
            for (var i = 0; i < this.dataFormList.length; i++) {
              this.statusList.push(false);
            }
            this.dataFormListStatus = false;
          } else {
            this.dataFormListStatus = true;
          }
          // 判断列表没有数据是显示空组件
          if (this.dataFormList.length == 0) {
            this.dataFormListStatus = true;
          } else {
            this.dataFormListStatus = false;
          }
          this.loadingShow = false;
        });
      }
    },
    // 获取备注
    GetCustomerRemark(CustId, index) {
      var UofIds = {
        CustId: CustId,
      };
      POST_GetCustomerRemark(UofIds).then((response) => {
        this.dataFormList[index].Remark = response.Data.Remark;
        this.$forceUpdate();
      });
    },
    clickStatus: function(item, index) {
      var status = !this.statusList[index];
      this.statusList.splice(index, 1, status);
      // this.statusList.splice(index, 1, item);
      event.stopPropagation();
    },
    // 跳转到客户详情
    toCustomerDataDetails(id, CustPhone) {
      this.$router.push(
        "/CustomerService/CustomerData/CustomerDataDetails?id=" +
          id +
          "&CustPhone=" +
          CustPhone
      );
    },
  },
};
</script>

<style lang="scss" scoped>
.CustomerData {
  padding-right: 5.33%;
  padding-top: 24px;
  .CustomerDataBox {
    background: rgba(255, 255, 255, 1);
    border-radius: 8px;
    padding: 21px 5.37%;
    margin-bottom: 10px;
    .CustomerDataHeader {
      display: flex;
      align-items: center;
      .CustomerDataHeaderImgA {
        border-radius: 50%;
        width: 30px;
        height: 30px;
      }
      span {
        font-size: 16px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
        line-height: 22px;
        margin: 2.13%;
      }
      .CustomerDataHeaderImgB {
        width: 62px;
        height: 17px;
      }
      .CustomerDataHeaderImgRight {
        width: 9px;
        height: 16px;
        margin-left: auto;
      }
      .CustomerDataHeaderImgBottom {
        width: 15px;
        height: 8px;
        margin-left: auto;
      }
    }
    .CustomerDataContent {
      margin-top: 16px;
      h2 {
        font-size: 14px;
        font-weight: 500;
        color: rgba(61, 61, 61, 1);
        line-height: 20px;
      }
      p {
        margin-top: 12px;
        font-size: 12px;
        font-weight: 500;
        color: rgba(189, 189, 189, 1);
        line-height: 18px;
      }
      div {
        margin-top: 10px;
        margin-left: auto;
        text-align: center;
        background: linear-gradient(
          180deg,
          rgba(28, 118, 254, 1) 0%,
          rgba(28, 118, 254, 1) 100%
        );
        box-shadow: 0px 3px 12px 0px rgba(28, 118, 254, 0.3);
        border-radius: 6px;
        font-size: 14px;
        font-weight: 500;
        color: rgba(255, 255, 255, 1);
        line-height: 30px;
        width: 86px;
        height: 30px;
      }
    }
  }
}
</style>
